::before * ** ::after {
  box-sizing: border-box;
}
#darkMode {
  /* background: transparent url("./images/moon.png") no-repeat; */
  background-size: 30px 30px;
  width: 32px;
  height: 32px;
  filter: grayscale(100%);
  border: solid black;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
}
#darkMode:hover,
#darkMode:focus {
  filter: none;
  background-color: black;
  cursor: pointer;
}
#darkMode:clicked {
  filter: none !important;
  background-color: black;
}
:root {
  --blue: rgb(26, 57, 87);
  --white: rgb(236, 236, 236);
  --black: rgb(0, 0, 0);
}
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: var(--background-color);
  color: var(var(--color));
  font-weight: var(--font-weight);
  transition: background-color 0.3s ease-in-out, color 0.6s ease-in-out;
  justify-content: center;
  align-items: center;
}
body.light {
  --background-color: var(--white);
  --text-color: var(--blue);
  --font-weight: 400;
  --image: url("./images/fashion1.jpg");
}

body.dark {
  --background-color: var(--blue);
  --text-color: var(--white);
  --font-weight: 500;
  /* --image: url("./images/fashion2.jpg"); */
}
h1 {
  color: var(--text-color);
  text-align: center;
  word-wrap: break-word;
  font-size: 5rem;
  text-shadow: 0 0 5px red;
}
h2 {
  color: var(--text-color);
  word-wrap: break-word;
  font-size: 3rem;
  text-shadow: 0 0 3px black;
  align-content: center;
  justify-content: center;
}

.homepage {
  background-image: var(--image);
  background-position: center;
  background-size: cover;
  padding: 20px;
  margin: 0%;
  border-radius: 10px;
  align-items: center;
  overflow: hidden;
  position: flex;
  flex-direction: row;
}
@media screen and (max-width: 900px) {
  .homepage {
    width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .homepage {
    width: 100%;
  }
}

.Nav {
  margin-bottom: 50px;
  width: 100% auto;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
  overflow: hidden;
}

.Site-title {
  font-size: 2rem;
}
.Nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}
.Nav a {
  background: var(--background-color);
  color: var(--text-color);
  border-radius: 30px;
  text-decoration: none;
  height: 30px;
  display: flex;
  align-items: center;
  padding: 0.25rem;
}
.Nav li:hover {
  background-color: #777;
  border-radius: 30px;
  height: 30px;
}
.buttonNav {
  background: transparent url("./images/cart.png") no-repeat;
  background-size: 30px 30px;
  width: 32px;
  height: 32px;
  border: none;
  position: relative;
}
.rounded-circle {
  background-color: red;
  background-size: 20px 20px;
  width: 16px;
  height: 16px;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  position: absolute;
  color: white;
  bottom: 0;
  right: 0;
  transform: translate(25%, 25%);
}
@media screen and (max-width: 900px) {
  .Nav,
  .Nav ul,
  .Site-title,
  .Nav a,
  h1 {
    width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .Nav,
  .Nav ul,
  .Site-title,
  .Nav a,
  hi {
    width: 100%;
  }
}
